import ISuiteNavData from './ISuiteNavData';

interface ISuiteNavModel {
  // Data that will be passed to O365Shell.SetData and O365Shell.Render.
  NavBarData: ISuiteNavData;

  /** URL for suite nav JS */
  JsUrl: string;

  /** URL for suite nav CSS */
  CssUrl: string;

  /** Unique user identifier. Only used for caching. */
  UserIdentifier?: string;

  /** Token that will be used to determine whether cached data is still valid. */
  CacheToken?: string;
}

export default ISuiteNavModel;
